/** @format */

body {
    font-family: "Roboto", sans-serif;
    background-color: #4bbeac;
    font-weight: bold;
    font-size: 70px;
}

.container {
    height: 100vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.outer-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.inner-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.box1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 100px;
    border-bottom: 8px solid #3ea192;
    border-right: 8px solid #3ea192;
}

