.X {
    color: #545454;
}

.O {
    color: #f2ebd3;
}

.final {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.final_text {
    margin: 0;
    margin-bottom: 10px;
}

.final_button {
    transition: all 0.5s;
    background-color: #229281;
    color: white;
    text-transform: uppercase;
    border-radius: 5px;
    border: 0;
    padding: 10px;
}

.final_button:hover {
    background-color: #1c796b;
}
